/* ==========================================================================
  BOOTSTRAP GRID SYSTEM OVERRIDE
=============================================================================
  Default: Breakpoint  |  Content width
    sm:         576px  |  540px
    md:         768px  |  720px
    lg:         992px  |  960px
    xl:        1200px  |  1140px
    xxl:       1400px  |  1320px

  Add-on:
    3xl:       1800px  |  1400px
    4xl:       2500px  |  1600px
========================================================================== */

/* BREAKPOINT - !! this variabes does not replace BS default !! */
$breakpoint-xs:      375px; // Mobile portrait
$breakpoint-sm:      576px; // Mobile landscape
$breakpoint-md:      768px; // iPad
$breakpoint-lg:      992px; // iPad Pro
$breakpoint-xl:     1200px; // Laptop (*Trigger mobile menu below this)
$breakpoint-xxl:    1400px; // Desktop
$breakpoint-3xl:    1600px; // Add-on: HD 1920px
$breakpoint-4xl:    2400px; // Add-on: QHD 2560px

/* CONTENT WIDTH */
$content-width-sm:   100%; // Mobile    540px
$content-width-md:   720px; // iPad      720px
$content-width-lg:   960px; // iPad Pro  960px
$content-width-xl:  1140px; // Laptop   1140px
$content-width-xxl: 1320px; // Desktop  1320px
$content-width-3xl: 1400px; // Add-on:  HD 1400px
$content-width-4xl: 1600px; // Add-on:  QHD 1600px

/* CUSTOM GUTTER */
.container, .container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl, .row, .row>* {
  --bs-gutter-x: 2rem; //Default 24px (1.5rem)

  /* MOBILE */
  @media only screen and (max-width: 767px) {
    --bs-gutter-x: 1rem;
  }
  /* LARGE SCREEN */
  @media only screen and (min-width: $breakpoint-4xl) {
    --bs-gutter-x: 2rem;
  }
}


/* CONTAINER SIDE PADDING */
.container, .container-fluid, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
  width: unset; //Prevents container overflow
  box-sizing: content-box; //Prevents content width shrinking
  padding-right: calc(var(--bs-gutter-x) * 1); //Adds extra padding to container side
  padding-left:  calc(var(--bs-gutter-x) * 1);  //Adds extra padding to container side
}


/* CUSTOM CONTAINER WIDTH
========================================================================== */
//MD
@media only screen and (min-width: 576px) {
  .container, .container-sm {
    max-width: $content-width-sm;
  }
}
@media only screen and (min-width: 768px) {
  .container, .container-sm, .container-md {
    max-width: $content-width-md;
  }
}

//LG
@media only screen and (min-width: 992px) {
  .container, .container-sm, .container-md, .container-lg {
    max-width: $content-width-lg;
  }
}

//XL
@media only screen and (min-width: 1200px) {
  .container, .container-sm, .container-md, .container-lg, .container-xl {
    max-width: $content-width-xl;
  }
}

//XXL
@media only screen and (min-width: 1400px) {
  .container, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
    max-width: $content-width-xxl;
  }
}

//3XL
@media only screen and (min-width: $breakpoint-3xl) {
  .container, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
    max-width: $content-width-3xl;
  }
}

//4XL
@media only screen and (min-width: $breakpoint-4xl) {
  .container, .container-sm, .container-md, .container-lg, .container-xl, .container-xxl {
    max-width: $content-width-4xl;
  }
}





/* ==========================================================================
  SCAFFOLDING
========================================================================== */
