/* ==========================================================================
  LINK
========================================================================== */

/*
  !NOTE: Do not set visited style globally
  a:visited { }
*/

// Reset
a:not([href]):not([class]),
a:not([href]):not([class]):hover {
  color: inherit;
  text-decoration: none;
}

// Prevent iPhone changing link color
a[href^="tel"]{
  color:inherit;
  text-decoration:none;
}
a[x-apple-data-detectors] {
  color: inherit !important;
  text-decoration: none !important;
  font-size: inherit !important;
  font-family: inherit !important;
  font-weight: inherit !important;
  line-height: inherit !important;
}



@mixin link-style {
  position: relative;
  text-decoration: none;

  &::after {
    content: "";
    position: absolute;
    bottom: -3px;
    height: 1px;
    left: 0px;
    right: 0px;

    background-color: #000;

    transform: scaleX(0);
    transform-origin: left center;
    transition: transform 250ms ease;
  }

  &:hover {
    &::after {
      transform: scaleX(100%);
    }
  }
}//@mixin link-style


@mixin link-underline {
  @include link-style;

  &::before {
    content: "";
    position: absolute;
    bottom: -3px;
    height: 1px;
    left: 0px;
    right: 0px;

    background: #FA6941;
  }
  &::after {
    transform-origin: right center;
    transform: scaleX(100%);
    transition: transform 250ms ease;
  }

  // Hover
  &:hover {
    &::after {
      transform: scaleX(0);
      transform-origin: right center;
    }
  }

  // Disabled
  &.disabled,
  &[aria-disabled="true"] {
    &::before {
      display: none;
    }
    &::after {
      background: #999;
    }
  }

}//@mixin link-underline


main,
.site-footer__wrapper {

  a {
    color: #000;
    text-decoration: none;

    &:hover {
      color: #000;
    }
    &:active {
      color: #FA6941;
      &::after {
        background-color: #FA6941;
      }
    }
    &:focus-visible {
      color: #FA6941;
      outline: 2px solid #000;
    }
    &.disabled,
    &[aria-disabled="true"] {
      color: #999;
      pointer-events: none;
    }
  }

  a {
    @include link-style;
  }

  a.link_underline {
    @include link-underline;
  }

}//main






/* ==========================================================================
  BUTTONS
========================================================================== */

// Link reset
a.btn {
  &::before,
  &::after {
    display: none;
  }
}

// Reset hover style
.btn-check:checked+.btn,
.btn.active,
.btn.show,
.btn:first-child:active,
:not(.btn-check)+.btn:active {
  color: #FFF;
  background: #3D3D3D;
  border: 1px solid #000;

  &.btn-inverted {
    color: #FFF;
    background: #3D3D3D;
    border: 1px solid #FFF;
  }
//
}



.btn {
  padding: 14px 12px 13px;
  min-width: 200px;
  min-height: 40px;
  box-sizing: border-box;
  background: transparent;
  border: 1px solid #000;
  border-radius: 50px;

  font-family: 'Adobe Caslon Pro';
  font-size: 11px;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 1px;
  color: #000;
  text-align: center;
  text-transform: uppercase;

  &:hover {
    color: #FFF;
    background: #000;
    border: 1px solid #000;
  }
  &:focus-visible {
    color: #FFF;
    background: #000;
    box-shadow: 0 0 0 3px #FA6941;
    border-color: #000;
  }

  @media only screen and (min-width: $breakpoint-3xl) {
    padding: 17px 15px 16px;
    min-width: 240px;
    min-height: 48px;
    font-size: 13px;
  }
//
}


// Primary Button, White Filled
.btn-primary {
  background: #FFF;
  border: 1px solid #FFF;
}

// Secondary Button, Black Filled
.btn-secondary {
  color: #FFF;
  background: #000;
  border: 1px solid #000;
  &:hover {
    color: #FFF;
    background: #3D3D3D;
  }
  &:focus-visible {
    color: #FFF;
    background: #3D3D3D;
    box-shadow: 0 0 0 3px #000;
    border-color: #000;
  }
}

// Button for dark background
.btn.btn-inverted {
  color: #FFF;
  background: transparent;
  border: 1px solid #FFF;

  &.btn-primary {
    color: #000;
    background: #FFF;
    border: 1px solid #FFF;
  }

  &:hover {
    color: #000;
    background: #FFF;
    border: 1px solid #FFF;
  }
  &:focus-visible {
    color: #000;
    background: #FFF;
    box-shadow: 0 0 0 3px #F5A790;
    border-color: #FFF;
  }
}


// Button Inverted Active
.btn.btn-inverted:active,
.btn-check:checked + .btn.btn-inverted,
.btn.active.btn-inverted,
.btn.show.btn-inverted,
.btn:first-child:active.btn-inverted,
:not(.btn-check) + .btn:active.btn-inverted {
  color: #000;
  background: #FFF;
  border: 1px solid #FFF;
}


// Disabled Button
.btn.disabled,
.btn[aria-disabled="true"],
button:disabled,
button[disabled] {
  color: #FFF;
  background-color: #BABABA;
  border: 1px solid #BABABA;
  pointer-events: none;
}





/* ==========================================================================
  INPUT
========================================================================== */
input {
  border: 1px solid #000000;
  border-radius: 50px;
  font-family: 'Adobe Caslon Pro';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.16px;

  padding: 8px 20px;

  &::placeholder {
    color: inherit;
    opacity: 1; /* Firefox */
    font-style: italic;
  }
}






/* ==========================================================================
  MEDIA
========================================================================== */
figure img {
  width: 100%;
  height: auto;
}

figure svg {
  width: auto;
  height: auto;
}





/* ==========================================================================
  SITE HEADER
========================================================================== */

/* HEADER LAYOUT */
.site-header {
  position: relative;
  z-index: 2;
  margin-bottom: -60px;

  @media only screen and (min-width: $breakpoint-3xl) {
    margin-bottom: -68px;
  }
}




/* NAVBAR */
.navbar {
  position: relative;
  height: 60px;
  padding: 0;

  &__wrapper {
    flex-grow: 1; // Fix the nav issue not going full-width when use custom gutter code
  }

  @media only screen and (min-width: 992px) {
    width: 100%;
    justify-content: end;

    &__wrapper {
      height: 100%;
    }
    &__wrapper::after {
      content: "";
      position: absolute;
      display: block;
      border-bottom: 1px solid #000;
      height: 1px;
      width: auto;
      left: 0;
      right: 0;
      bottom: 0;
      margin-right: calc(var(--bs-gutter-x) * 1);
      margin-left: calc(var(--bs-gutter-x) * 1);
    }
  }

  @media only screen and (min-width: $breakpoint-3xl) {
    height: 68px;
  }

}//.navbar



// Add class to body
@media only screen and (min-width: 992px) {
  .homepage .navbar__wrapper::after {
    border: 0;
  }
}



/* LOGO */
.navbar-brand__logo {
  position: relative;
  display: block;
  width: 18px;
  height: 18px;

  svg {
    vertical-align: top;
    width: auto;
    height: auto;
  }
  @media only screen and (min-width: 992px) {
    display: inline-block;
    width: 20px;
    height: 20px;
    top: 6px;
    margin-right: 16px;
  }
  @media only screen and (min-width: $breakpoint-3xl) {
    width: 24px;
    height: 24px;
    top: 3px;
    margin-right: 24px;
  }
}//.navbar-brand__logo



/* BRAND TITLE */
.navbar-brand__title {
  display: inline-block;
  font-family: 'Roslindale Variable Display';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;

  letter-spacing: 0.8px;
  margin-left: 1px;

  @media only screen and (max-width: 991px) {
    margin-left: 1px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%,-50%,0);
  }

  @media only screen and (min-width: 992px) {
    position: relative;
  }

  @media only screen and (min-width: $breakpoint-3xl) {
    font-size: 20px;
    line-height: 24px;
  }
}//.navbar-brand__title

@media only screen and (min-width: 992px) {
  .homepage .navbar-brand__title {
    display: none;
  }
}


/* NAVIGATION: PRIMARY
========================================================================== */
@media only screen and (min-width: 992px) {
  .site-header .navbar-nav {
    position: relative;
    width: 100%;
    justify-content: end;
    right: -16px;

    .nav-item {
      padding-right: 16px;
      padding-left: 16px;
    }
    .nav-link {
      font-family: 'Adobe Caslon Pro';
      font-style: normal;
      font-weight: 400;
      font-size: 13px;
      line-height: 20px;
      letter-spacing: 0;
      color: #000;
      padding: 0;
      @include link-style;

      &::after {
        bottom: 0px;
      }
    }
  }

}//@media


@media only screen and (min-width: $breakpoint-3xl) {
  .site-header .navbar-nav .nav-link {
    font-size: 16px;
    line-height: 20px;
  }
}//@media


/* Menu Mobile */
@media only screen and (max-width: 991px) {
  .site-header .offcanvas {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: none;
    transition: all 0ms ease-in-out;
  }

  .site-header .offcanvas-body {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 480px;
    padding-top: 60px;
    overflow-y: auto;
  }

  .site-header .offcanvas-header a,
  .site-header .offcanvas-body a {
    &,
    &:hover,
    &:focus {
      color: #FFFFFF;
    }
  }

  .site-header .navbar-nav {

    // Top Navbar cover for scrolling
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      background: #000;
      height: 60px;
    }
  }

  .site-header .nav-link {
    font-family: 'Roslindale Variable Display';
    font-style: normal;
    font-weight: 300;
    font-size: 28px;
    line-height: 26px;
    text-align: center;
    letter-spacing: 1px;
    text-transform: uppercase;
    padding: 3.9vh 0;
  }
//
}

@media only screen and (max-width: 767px) {
  .site-header .nav-link {
    font-size: 20px;
    line-height: 18px;
    padding: 16px 0;
  }
}

// Make nav menu scrollable for small screen and landscape mode
@media only screen and (max-width: 767px) and (max-height: 480px) {
  .site-header .navbar-nav {
    min-height: 480px;
  }
  .site-header .offcanvas-body {
    display: block;
  }
}


/* Mobile menu toggle */
.navbar-toggler {
  position: relative;
  right: -15px;
  width: 50px;
  height: 60px;
  border: 0;
  border-radius: 0;

  &:focus,
  &.btn-close:focus {
    box-shadow: none;
  }

  &__mobile-menu {
    display: block;
    width: 20px;
    height: 12px;
    margin: auto;
  }

  &__mobile-menu svg {
    vertical-align: top;
    width: 100%;
    height: auto;
  }

  /* Close button */
  &__mobile-menu::after {
    content: "";
    display: block;
    position: absolute;
    top: 22px;
    right: 18px;
    width: 15px;
    height: 15px;
    background:     url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCA2MCA2MCIgeG1sOnNwYWNlPSJwcmVzZXJ2ZSI+PHBhdGggZD0iTTU3LjEgMCAwIDU3LjEgMi45IDYwIDYwIDIuOSA1Ny4xIDB6TTAgMi45IDU3LjEgNjBsMi45LTIuOUwyLjkgMCAwIDIuOXoiIGZpbGw9IiNGRkYiIC8+PC9zdmc+') no-repeat center center;
  }
}//.navbar-toggler



/* Custom toggle animation */
@media only screen and (max-width: 991px) {
  .navbar-toggler__mobile-menu::after,
  #siteNavbar .offcanvas-body,
  #siteNavbar.show .navbar-toggler svg {
    opacity: 0;
  }

  #siteNavbar.show .offcanvas {
    visibility: visible;
  }

  #siteNavbar.show .offcanvas-body,
  #siteNavbar.show .navbar-toggler__mobile-menu::after {
    opacity: 1;
    transition: all 480ms ease-in-out;
  }

  #siteNavbar.show .navbar-toggler,
  #siteNavbar.show .navbar-brand__logo,
  #siteNavbar.show .navbar-brand__title {
    z-index: 99999;
    color: #FFF;
  }

  .offcanvas-backdrop.show {
    opacity: 1;
    }
}//@media










