/* ==========================================================================
  MARQUEE
========================================================================== */

.footer-marquee {
  height: 58px;
  overflow: hidden;
  position: relative;
  border-top: 1px solid;
  border-bottom: 1px solid;
  padding: 0;

  font-family: 'Roslindale Variable Display';
  font-style: normal;
  font-weight: 300;
  text-align: center;
  letter-spacing: 1px;
  text-transform: uppercase;

  &__inner {
    // top: 50%;
    // left: 0;
    // transform: translate3d(0, -50%, 0);
    position: absolute;
    width: max-content;
  }

  &__icon {
    width: 18px;
    height: 18px;
    display: inline-block;
    margin: 0 20px;

    svg {
      vertical-align: top;
      width: auto;
      height: auto;
      position: relative;
    }
  }

  strong {
    font-weight: inherit;
  }

  &__link {
    color: inherit;
    display: inline-block;
    font-size: 16px;
    line-height: 18px;
    font-weight: 300;
    font-style: none;
    text-decoration: none;
    height: 60px;
    padding: 20px 0 0;
    transition: all 250ms ease;
  }
  &__link:hover {
    color: #FFF;
    background: #000;
  }
  @media only screen and (min-width: 768px) {
    & {
      height: 80px;
    }
    &__link {
      height: 80px;
      font-size: 28px;
      line-height: 26px;
      padding-top: 26px;
    }
    &__icon {
      width: 24px;
      height: 24px;
    }
  }

  @media only screen and (min-width: $breakpoint-3xl) {
    & {
      height: 119px;
    }
    &__link {
      height: 119px;
      font-size: 36px;
      line-height: 32px;
      padding-top: 44px;
    }
    &__icon {
      width: 32px;
      height: 32px;
    }
  }
}



.cssmarquee {
  --move-start: calc(0);
  --move-end: calc(-50%);
  width: fit-content;
  display: flex;
  position: relative;
  transform: translate3d(var(--move-start), 0, 0);
}

@keyframes cssmarquee {
  0% {
    transform: translate3d(var(--move-start), 0, 0);
  }
  100% {
    transform: translate3d(var(--move-end), 0, 0);
  }
}

// Trigger animation only when visible
.footer-marquee.aos-animate .cssmarquee {
  animation: cssmarquee 30s linear infinite;
}






