========================================================================== */
  $base-image: url("../images/holographic-moon--300%403x.png");

  :root {
    --color1: #F5A790;
    --color2: #C5D5EE;
  }


/* Moon Size
========================================================================== */
  .holomoon {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
    z-index: 1;

    background-color: transparent;
    background-image: $base-image;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: 110%;

    transform-origin: center;
    transition: transform 0.5s ease, box-shadow 0.2s ease;
    will-change: transform, filter;
    box-shadow: none;
  }



/* Moon Hover Halo Shadow
========================================================================== */
  .holomoon:hover {
    box-shadow:
      -20px -20px 30px -25px var(--color1),
      20px 20px 30px -25px var(--color2),
      -7px -7px 10px -5px var(--color1),
      7px 7px 10px -5px var(--color2),
      0 0 13px 4px rgba(255,255,255,0.3);
  }



/* Moon Glow Overlay Layers
========================================================================== */
  .holomoon-glow,
  .holomoon:before,
  .holomoon:after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background-repeat: no-repeat;
    opacity: .5;
    mix-blend-mode: color-dodge;
    transition: all .33s ease;
    border-radius: 50%;
    overflow: hidden;
  }


  /* Moon Glow 1 - Prime Colour Glow Overlay */
  .holomoon-glow {
    background-position: 50% 50%;
    background-size: 300% 300%;
    background-repeat: repeat-x;
    background-size: 200vw 100%;
    background-image: linear-gradient(
      115deg,
      transparent 0%,
      var(--color1) 25%,
      transparent 47%,
      transparent 53%,
      var(--color2) 75%,
      transparent 100%
    );
    opacity: 0.7;
    filter: brightness(.5) contrast(1);
    z-index: 1;
    transition: all 30ms ease-in-out;
  }

  .holomoon:before {
    background-position: 50% 50%;
    background-size: 300% 300%;
    background-repeat: repeat-x;
    background-size: 200vw 100%;
    background-image: linear-gradient(
      115deg,
      transparent 0%,
      var(--color1) 25%,
      transparent 47%,
      transparent 53%,
      var(--color2) 75%,
      transparent 100%
    );
    opacity: 0.1;
    filter: brightness(.5) contrast(1);
    z-index: 1;
  }

  /* Modern browser only - Safari 15+, Chrome 88+  */
  @supports (aspect-ratio:1) {
    /* Moon Glow 2 - Subtle Rainbow Gradient Overlay */
    .holomoon:after {
      background-image:
        linear-gradient(125deg, #ff008450 15%, #fca40040 30%, #ffff0030 40%, #00ff8a20 60%, #00cfff40 70%, #cc4cfa50 85%);
      background-position: 50% 50%;
      background-size: 160%;
      background-blend-mode: overlay;
      z-index: 2;
      filter: brightness(1) contrast(1);
      transition: all .33s ease;
      mix-blend-mode: color-dodge;
      opacity: 1;
    }
  }

  // .holomoon.active:after,
  // .holomoon:hover:after {
  //   filter: brightness(1) contrast(1);;
  //   opacity: 1;
  // }

  // .holomoon.active,
  // .holomoon:hover {
  //   // animation: none;
  //   transition: box-shadow 0.1s ease-out;
  // }



/* Moon Hover Reflection
========================================================================== */
  .holomoon-glow.active,
  .holomoon:hover:before {
    animation: none;
    background-image: linear-gradient(
      110deg,
      transparent 25%,
      var(--color1) 48%,
      var(--color2) 52%,
      transparent 75%
    );
    background-position: 50% 50%;
    background-size: 250% 250%;
    opacity: .5;
    filter: brightness(.66) contrast(1.33);
    // transition: none;
  }


  // .holomoon.active:before,
  // .holomoon:hover:before,
  // .holomoon.active:after,
  // .holomoon:hover:after {
  //   animation: none;
  //   transition: none;
  // }


/* Animation
========================================================================== */
  .holomoon.animated {
    &::before {
      animation: holoGlow2 20s ease 0s infinite;
    }
    &::after {
      animation: holoGradient 12s ease 0s infinite;
    }
  }


  @keyframes holoGradient {
    0%, 100% {
      opacity: .75; background-position: 50% 50%; filter: brightness(1.2) contrast(1.25);
    }
    5%, 8% {
      opacity: 1; background-position: 40% 40%; filter: brightness(.8) contrast(1.2);
    }
    13%, 16% {
      opacity: .5; background-position: 50% 50%; filter: brightness(1.2) contrast(.8);
    }
    35%, 38% {
      opacity: 1; background-position: 60% 60%; filter: brightness(1) contrast(1);
    }
    55% {
      opacity: .33; background-position: 45% 45%; filter: brightness(1.2) contrast(1.25);
    }
  }



  @keyframes holoGlow {
    0%, 100% {
      opacity: 0.5;
      // background-position: 50% 50%;
      filter: brightness(.5) contrast(1);
    }
    5%, 9% {
      // background-position: 100% 100%;
      opacity: 1;
      filter: brightness(.75) contrast(1.25);
    }
    13%, 17% {
      // background-position: 0% 0%;
      opacity: .88;
    }
    35%, 39% {
      // background-position: 100% 100%;
      opacity: 1;
      filter: brightness(.5) contrast(1);
    }
    55% {
      // background-position: 0% 0%;
      opacity: 1;
      filter: brightness(.75) contrast(1.25);
    }
  }


  @keyframes holoGlow2 {
    0%, 100% {
      opacity: 0.7;
      background-position: 50% 50%;
      filter: brightness(.5) contrast(1);
    }
    5%, 9% {
      background-position: 100% 100%;
      opacity: 0.2;
      filter: brightness(.75) contrast(1.25);
    }
    13%, 17% {
      background-position: 0% 0%;
      opacity: .8;
    }
    35%, 39% {
      background-position: 100% 100%;
      opacity: 0.1;
      filter: brightness(.5) contrast(1);
    }
    55% {
      background-position: 0% 0%;
      opacity: 0.7;
      filter: brightness(.75) contrast(1.25);
    }
  }






